.btn{
 font-size: 1.2rem;
    padding: 0.5rem 1.5rem;
    border: 1px solid #75034f;
    color: white;
    background: #8b005d;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.26);
    cursor: pointer;
}
  
  .btn:hover,
  .btn:active {
    background: #ac0e77;
    border-color: #ac0e77;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.26);
  }